<template>
    <el-container>
        <el-header>
            <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
            <el-button type="info" @click="back()" icon="el-icon-back">返回</el-button>
        </el-header>
        <el-main>

            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">

                <el-form-item label="Banner" prop="url">
                    <!-- element 上传图片按钮 -->
                    <el-upload class="upload-demo" action="" drag accept="image/jpeg,image/gif,image/png"
                               :auto-upload="false" :show-file-list="false" :on-change='changeUpload'>
                        <img :src="ruleForm.url" class="pic">
                        <i v-show="upload" class="el-icon-upload"></i>
                        <div v-show="upload" class="el-upload__text">点击上传</div>
                    </el-upload>
                </el-form-item>

                <el-form-item label="排序号" prop="sortIndex">
                    <el-input-number v-model="ruleForm.sortIndex" :min="1" :max="99" label="请输入排序号"></el-input-number>
                </el-form-item>
            </el-form>
        </el-main>

        <!-- vueCropper 剪裁图片实现-->
        <el-dialog title="剪裁图片" :visible.sync="dialogVisible" append-to-body>
            <div class="cropper-content">
                <div class="cropper" style="text-align:center">
                    <vueCropper
                            ref="cropper"
                            :img="option.img"
                            :outputSize="option.size"
                            :outputType="option.outputType"
                            :info="true"
                            :full="option.full"
                            :canMove="option.canMove"
                            :canMoveBox="option.canMoveBox"
                            :original="option.original"
                            :autoCrop="option.autoCrop"
                            :fixed="option.fixed"
                            :fixedNumber="option.fixedNumber"
                            :centerBox="option.centerBox"
                            :infoTrue="option.infoTrue"
                            :fixedBox="option.fixedBox"
                            @realTime="realTime"
                    ></vueCropper>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取消</el-button>
                <el-button type="primary" @click="finish" :loading="loading">上传</el-button>
            </div>
        </el-dialog>

    </el-container>
</template>

<script>

    export default {
        data() {
            return {
                previews: {},
                upload: true,
                dialogVisible: false,
                loading: false,
                ruleForm: {
                    id: '',
                    sortIndex: '',
                    fid: '',
                    url: '',
                },
                option: {
                    img: '', // 裁剪图片的地址
                    info: true, // 裁剪框的大小信息
                    outputSize: 1, // 裁剪生成图片的质量
                    outputType: 'png', // 裁剪生成图片的格式
                    canScale: true, // 图片是否允许滚轮缩放
                    autoCrop: true, // 是否默认生成截图框
                    // autoCropWidth: 4000, // 默认生成截图框宽度
                    // autoCropHeight: 200, // 默认生成截图框高度
                    fixedBox: false, // 固定截图框大小 不允许改变
                    fixed: true, // 是否开启截图框宽高固定比例
                    fixedNumber: [144, 36], // 截图框的宽高比例
                    full: true, // 是否输出原图比例的截图
                    canMoveBox: true, // 截图框能否拖动
                    original: false, // 上传图片按照原始比例渲染
                    centerBox: true, // 截图框是否被限制在图片里面
                    infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
                },
                rules: {
                    sortIndex: [
                        {
                            required: true,
                            message: '请输入排序号',
                            trigger: 'blur'
                        },
                    ],
                    url: [
                        {
                            required: true,
                            message: '请上传Banner',
                            trigger: 'blur'
                        },
                    ],

                }
            }
        },
        mounted() {
            if (this.$route.query.id != undefined) {
                this.ruleForm.id = this.$route.query.id;
                this.axios.get('/api/banner/get/' + this.$route.query.id).then((response) => {
                    this.ruleForm.sortIndex = response.data.data.sortIndex;
                    this.ruleForm.url = response.data.data.url;
                    this.ruleForm.fid = response.data.data.fid;
                    this.upload = false;
                }).catch((response) => {
                    console.log(response);
                })
            }
        },
        methods: {
            // 上传按钮
            changeUpload(file) {
                //上传图片
                const isIMAGE = (file.raw.type === 'image/jpeg' || file.raw.type === 'image/png' || file.raw.type === 'image/gif');
                if (!isIMAGE) {
                    this.$message({
                        type: 'info',
                        message: '上传文件只能是图片格式!'
                    });
                    return false;
                }
                var reader = new FileReader();
                reader.readAsDataURL(file.raw);
                let that = this;
                reader.onload = function () {
                    that.dialogVisible = true;
                    that.option.img = this.result;
                }
            },
            realTime(data) {
                this.previews = data;
            },
            // 点击裁剪，这一步是可以拿到处理后的地址
            finish() {
                this.$refs.cropper.getCropBlob((data) => {
                    this.loading = true

                    let formData = new FormData()
                    formData.append('file', data);

                    let config = {
                        headers: {'Content-Type': 'multipart/form-data'}
                    }
                    // 添加请求头
                    this.axios.post('/api/upload/file', formData, config).then(response => {
                        if (response.data.state == 0) {
                            this.dialogVisible = false;
                            console.log(response.data.data)
                            this.ruleForm.url = response.data.data;
                            this.upload = false;
                            this.loading = false;
                        } else {
                            this.loading = false;
                            this.$message({
                                type: 'info',
                                message: '上传出错'
                            });
                        }
                    })
                })
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.axios.post('/api/banner/save', {
                            ...this.ruleForm
                        }).then((response) => {
                            if (response.data.state == '0') {
                                this.back();
                            } else {
                                this.$message({
                                    type: 'info',
                                    message: '保存失败'
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            back() {
                this.$router.push({path: '/bannerManage/index', replace: true});
            }
        }
    }
</script>
<style>
    .el-upload-dragger {
        /*max-width: 432px;*/
        /*max-height: 108px;*/
        min-width: 432px;
        width: 100%;
    }
</style>
<style scoped>
    .demo-ruleForm {
        width: 100%;
    }

    .pic {
        max-width: 100%;
    }

    .cropper-content {
        width: auto;
        height: 400px;
    }

    .cropper {
        width: auto;
        height: 400px;
    }
</style>
